  //     hashTable[date] = hashTable[date] ||{title:date, item:[]}
  //     hashTable[date].items.push(recordList[i])
  //   }
  //   return hashTable
  // }

  beforeCreate() {
    this.$store.commit('fetchRecords');
  }

  type = '-';
  interval = 'day';
  intervalList = intervalList;
  typeList = typeList;
}
